@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  font-family: "Open Sans", serif;
  background: #EECDA3;
  background-image: url('https://cdn.dribbble.com/users/844597/screenshots/5986183/attachments/1286961/fog_texture.png');
  min-height: 100vh;
  background-repeat: no-repeat;
  background-size: 100% 110%;
}

.inputbar {
  display: -webkit-flex;
  display: flex;
  padding: 12px 14px;
  width: 400px;
  font-size: 32px;
}

.weather {
  width: 600px;
  background-color: blue;
}

.btn {
  border-width: 0;
  outline: none;
  border-radius: 3px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, .6);
  background: coral;
  color: whitesmoke;
  font-size: 16px;
}

.board {
  min-width: 900px;
  padding: 25px;
  background-image: -webkit-linear-gradient(315deg, #fdfcfb 0%, #e2d1c3 100%);
  background-image: linear-gradient(135deg, #fdfcfb 0%, #e2d1c3 100%);
}

.App {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
